.home__container{
    width: 100%;
    height: 100%;
 
    display: flex;
    justify-content: center;
    align-items: center;
}
.home__content{
    display: flex;
    flex-direction: column;
    /* width: 40%; */
    padding: 1rem 3rem;


    /* background-image: linear-gradient(to bottom,#66fcf1,#0c756e2c); */
    background-image: linear-gradient(to bottom,#66fcf2cc,#66fcf1cc);
    transition: all .3s;
    border-radius: 1rem;


}
.home__content:hover{
    background-image: linear-gradient(to bottom,#66fcf2dd,#66fcf1dd);
    box-shadow: 0rem 0rem 2rem #66fcf1;
}
.home__title{
    text-transform: uppercase;
    color: #1f2833;
    text-align: center;

    font-family: sans-serif;
    font-size: 3rem;
    letter-spacing: .5rem;

    width: 100%;

    padding: 2rem;


    
}
.home__form{
    align-self: center;
    width: 87%;
    padding-bottom: 3rem;
}

@media only screen and (max-width:500px){
    .home__title{
        font-size: 2.3rem;
    }
    .home__content{
        padding: 1rem 0rem;
    }
}
