.homeForm__container{
    width:100%;
}
.homeForm__form{
    width:100%;
    height: 4rem;

    display:flex;
    justify-content: center;

}
.homeForm__nameInput{
    width:70%;
    height: 100%;

    border-radius: 1rem 0 0 1rem;
    font-size: 2rem;
    padding: .5rem 1rem;
    color: #1f2833;

    border: none;
}
.homeForm__nameInput:focus{
    /* border: none !important; */
    outline-color: #1f2833;
}
.homeForm__nameSubmit{
    width: 30%;
    height: 100%;

    font-size: 2rem;
    text-transform: uppercase;
    background-color: #1f2833;
    color: #66fcf1;

    border-radius: 0 1rem  1rem 0;
    border: none;
    cursor: pointer;
}
.homeForm__error{
    font-size: 2rem;
    color:red;
    padding: 0 1rem;
}