.game__wrapper{
    width: 100%;
    height: 100%;

    display: flex;
}
.game__area{
    width: 50%;
    height:100%;
}
.game__content{
    width: 100%;
    height: 100%;
}
.game__detail-1{
    align-items: flex-start;
}
.game__detail-2{
    align-items: flex-end;

}
.game__detail{
    height: 15%;
    width: 100%;
    display: flex;
    flex-direction:column;

    background-color: #000000;

    padding: 2rem;
}
.game__name{
    font-size: 3rem;
    letter-spacing: .5rem;

    text-transform: uppercase;
    font-family: sans-serif;
    font-weight: 700;

    color: #66fcf1;
}
.game__progressBox{
    width: 50%;
}
.game__progress--invert{
    transform: rotateY(180deg);
}
.game__progress{
    /* color: black; */
    background-color: #66fcf1;
    height: 2rem;
    width: 100%;
}
.game__progress::-moz-progress-bar{
    background-color: #66fcf1;
}
.game__progress::-webkit-progress-value{
    background-color: #66fcf1;
}
.game__buttonArea{
    height: 85%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.game__button{
    width: 50%;
    height: 20rem;
    background-color: #66fcf1;
    color: #1f2833;
    font-size: 3rem;
    text-transform: uppercase;
    font-family: sans-serif;
    letter-spacing: .5rem;

    display: flex;
    justify-content: center;
    align-items: center;

    box-shadow: 1rem 1rem 1rem #000;
    cursor: pointer;
    transition: all .2s;

    user-select: none;
}

.game__button--click:active{
    box-shadow: none;
    transform: translate(.2rem,.2rem);
}
.game__button--press{
    box-shadow: none;
    transform: translate(.2rem,.2rem);
}
.game__divider{
    height: 100%;
    width: 1rem;
    background-color: #1f2833;
}

@media only screen and (max-width:900px){
    .game__button{
        width: 70%;
    }
}
@media only screen and (max-width:600px){
    .game__wrapper{
        flex-direction: column;
    }
    .game__area{
        width: 100%;
        height: 50%;
    }
    .game__divider{
        height: 1rem;
        width: 100%;
    }
    .game__detail{
        height: 25%;
    }
    .game__buttonArea{
        height: 75%;
    }
}