.winner__wrapper{
    width:100%;
    height: 100%;
    background-color: #66fcf1;

    display: flex;
    justify-content: center;
    align-items: center;
}
.winner__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: sans-serif;
    text-transform: uppercase;
    letter-spacing: .5rem;
    
}
.winner__text{
    color: #1f2833;
    font-size: 5rem;
    margin-bottom: 4rem;
}
.home__btn{
    color: #66fcf1;
    background-color: #1f2833;
    padding: 1rem 3rem;
    font-size: 3rem;
    cursor: pointer;
}

@media only screen and (max-width:400px) {
    .winner__text{
        font-size: 3rem;
    }
}